import { CellControllerManager } from '../table.cell_controller_manager';
import { AbstractCellHandler } from './handler.abstract';

export class CellInitializeWidgetHoverHandler extends AbstractCellHandler {
    public eventNames = ['CELL_HOVER'];

    public merger = 'uniqe';

    public name = 'EVENT_INITIALIZE_WIDGET';

    public action({ id }: Parameters<AbstractCellHandler['action']>[0] & { id: string }): void {
        const cellControllerManager = <CellControllerManager | undefined>(this.externalContext.getStickerManagers().get(CellControllerManager.NAME));

        cellControllerManager?.initializeWidget(id);

        cellControllerManager?.mountWidget(id);
    }
}
